.quiz{
    background: rgb(6, 4, 29);
    width: 100vw;
    height: 100vh;
    padding: 0 3rem;
    

    .score{
        color: white;
        font-size: 1.3rem;
        text-align: center;
        padding-top: 1rem;

    }
    
    .scoreBoard{
        color: black;
        text-align: center;
        background-color: white;
        width: 60%;
        height: 200px;
        border-radius: 1rem;
        font-size: 1.5rem;
        position: absolute;
        top: 40%;
        right: 50%;
        transform: translate(50%, -50%);
        font-weight: bold;
        padding: 1.5rem;

        .theMessage{
            font-size: 2rem;
            margin-bottom: .5rem;
            color: green;
        }
        .refresh{
            background: gold;
            padding: 1rem 2rem;
            border-radius: .6rem;
            border: none;
            font-size: 1.1rem;
            margin-top: 1rem;
            font-weight: bold;
            cursor: pointer;
        }
    }
   .container{
       text-align: center;
       .wrapper{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

        }
       .headings{
               color: white;
               padding: 2rem;
               font-size: 3rem; 
               text-transform: capitalize;

       }
       .tag{
           color: gold;
       }
       .questions{
           color: white;
           font-size: 1.5rem;
           padding: 2rem 0;
           font-weight: 400;
       }

       .actions{
           display: flex;
           flex-direction: column;
           width: 100%;
           align-items: center;
           justify-content: center;

           .btn{
               width: 100%;
               padding: 1rem;
               border-radius: .6rem;
               border: none;
               margin: .6rem 0;
               font-size: 1.2rem;
               cursor: pointer;
               text-transform: uppercase;
               transition: .3s ease;
               &:hover{
                   background: gold;
                   color: #020026;
               }
           }
       }
      
   } 
}

