.quiz {
  background: #06041d;
  width: 100vw;
  height: 100vh;
  padding: 0 3rem;
}

.quiz .score {
  color: white;
  font-size: 1.3rem;
  text-align: center;
  padding-top: 1rem;
}

.quiz .scoreBoard {
  color: black;
  text-align: center;
  background-color: white;
  width: 60%;
  height: 200px;
  border-radius: 1rem;
  font-size: 1.5rem;
  position: absolute;
  top: 40%;
  right: 50%;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  font-weight: bold;
  padding: 1.5rem;
}

.quiz .scoreBoard .theMessage {
  font-size: 2rem;
  margin-bottom: .5rem;
  color: green;
}

.quiz .scoreBoard .refresh {
  background: gold;
  padding: 1rem 2rem;
  border-radius: .6rem;
  border: none;
  font-size: 1.1rem;
  margin-top: 1rem;
  font-weight: bold;
  cursor: pointer;
}

.quiz .container {
  text-align: center;
}

.quiz .container .wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.quiz .container .headings {
  color: white;
  padding: 2rem;
  font-size: 3rem;
  text-transform: capitalize;
}

.quiz .container .tag {
  color: gold;
}

.quiz .container .questions {
  color: white;
  font-size: 1.5rem;
  padding: 2rem 0;
  font-weight: 400;
}

.quiz .container .actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.quiz .container .actions .btn {
  width: 100%;
  padding: 1rem;
  border-radius: .6rem;
  border: none;
  margin: .6rem 0;
  font-size: 1.2rem;
  cursor: pointer;
  text-transform: uppercase;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.quiz .container .actions .btn:hover {
  background: gold;
  color: #020026;
}

